


.videoEditing{
  display: grid;
  grid-template-rows: 4vw 4vw 4vw 4vw 4vw 4vw 4vw 4vw 4vw 4vw 4vw 4vw 4vw 4vw 4vw 4vw 4vw;
  grid-template-columns: 1fr;
  background-color: #141414;
  grid-row-gap: 1px;
}

.bg-rows{
  z-index: 0;
  background-color: #373737;
  grid-column: 1/2;
}

.secondGrid{
  grid-row: 6/13;
  grid-column: 1/2;
  display: grid;
  grid-template-rows: 4vw 4vw 4vw 4vw 4vw 4vw 4vw;
  grid-template-columns: 1fr;
  grid-row-gap: 1px;

  > .row1{
    grid-row: 1/2;
  }

  > .row2{
    grid-row: 2/3;
  }

  > .row3{
    grid-row: 3/4;
  }

  > .row4{
    grid-row: 4/5;
  }

  > .row5{
    grid-row: 5/6;
  }

  > .row6{
    grid-row: 6/7;
  }

  > .row7{
    grid-row: 7/8;
  }
}

.fx{
  background-image: url("../img/fxIcon.jpg");
  background-repeat: no-repeat;
  background-size: 1.8vw;
  background-position: 1.6vw 0.5vw;
}

